<template>
  <div class="carousel">
    <div class="focusMuscleWrap">
      <div class="imgWrap">
        <i class="uil uil-angle-left-b arrowIconL" v-show="!isImgHeader" @click="toggleCarousle('left')"></i>
        <i class="uil uil-angle-right-b arrowIconR" v-show="!isImgTail" @click="toggleCarousle('right')"></i>
        <transition :name="toggleImg">
          <img :src="imgArr[imgArrIndex]" :key="imgArr[imgArrIndex]" alt="" class="portfolioImg">  
        </transition>
        <!-- 使用絕對定位來確保動畫滑順 但需要解決imgWrap的高度塌陷 所以再開同一張圖片並設置完全透明 使BFC能夠計算高度 -->
        <img :src="imgArr[imgArrIndex]" alt="" class="fakeWrapOfImg">
      </div>
    </div>
    <div class="contentWrap">
      <Content>
        <!-- 暫時想不到解決方案 所以直接判斷條件來渲染內容 -->
        <div v-if="item == 'focusmuscle'">
          <h3>
            <a target="break" href="https://oz841119.github.io/focus-muscle/">FocusMuscle <i class="uil uil-link linkIcon"></i></a>
