        </div>
        <!-- 暫時想不到解決方案 所以直接判斷條件來渲染內容 -->
      </Content>
    </div>
  </div>
</template>

<script>
import Content from "./Content.vue"
export default {
  components: {
    Content
  },
  props: ['imgArr','item'],
  data() {
    return {
      isImgHeader: true,
      isImgTail: false,
      imgArrIndex: 0,
      toggleImg: 'toggleLeft'
    }
  },
  methods: {
    toggleCarousle(direction) {
      if(direction == 'right') {
        this.imgArrIndex += 1
        this.toggleImg = 'toggleLeft'
      }
      if(direction == 'left') {
        this.imgArrIndex -= 1
        this.toggleImg = 'toggleRight'
      }
    }
  },
  watch: {
    imgArrIndex: {
      handler: function() {
        this.imgArrIndex == 0 ? this.isImgHeader = true : this.isImgHeader = false
        this.imgArrIndex == this.imgArr.length - 1 ? this.isImgTail = true : this.isImgTail = false
      }
    }
  }
}
</script>

